            background-color="transparent"
        >
          <el-menu-item index="home">
            <i class="el-icon-house"></i>
            <span v-if="!isCollapsed"><el-icon><HomeFilled/></el-icon>‰∏ªÈ°µ</span>
            <span v-if="isCollapsed"><el-icon><HomeFilled/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="user">
            <i class="el-icon-document"></i>
            <span v-if="!isCollapsed"><el-icon><UserFilled/></el-icon>ÈÖçÁΩÆÂàóË°®</span>
            <span v-if="isCollapsed"><el-icon><UserFilled/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="createTask">
            <i class="el-icon-s-management"></i>
            <span v-if="!isCollapsed"><el-icon><Finished/></el-icon>‰ªªÂä°ÂàóË°®</span>
            <span v-if="isCollapsed"><el-icon><Finished/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="ociLog">
            <i class="el-icon-s-operation"></i>
            <span v-if="!isCollapsed"><el-icon><List/></el-icon>ÊúçÂä°Êó•Âøó</span>
            <span v-if="isCollapsed"><el-icon><List/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="sysCfg">
            <i class="el-icon-s-operation"></i>
            <span v-if="!isCollapsed"><el-icon><Tools/></el-icon>Á≥ªÁªüÈÖçÁΩÆ</span>
            <span v-if="isCollapsed"><el-icon><Tools/></el-icon></span>
          </el-menu-item>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <!-- Main Content Area -->
    <el-container>
      <el-header class="header">
        <span></span>
        <div class="right-buttons">
          <el-button v-show="needUpdate" @click="showUpdateDialog = true">üîî Êñ∞ÁâàÊú¨Ôºö{{ latestVersion }}</el-button>
          <el-button @click="toggleTheme" class="logout-button">{{ themeText }}</el-button>
          <button @click="showLogoutConfirm" class="logout-button">ÈÄÄÂá∫ÁôªÂΩï</button>
        </div>
      </el-header>
      <el-main>
        <router-view></router-view> <!-- Display area for switching content -->
      </el-main>
      <el-footer style="text-align: center;">
        <el-link href="https://github.com/Yohann0617" target="_blank">¬© Yohann All Rights Reserved {{ currentVersion }}
        </el-link>
      </el-footer>
    </el-container>
  </el-container>

  <el-dialog
      v-model="showUpdateDialog"
      title="ÁâàÊú¨Êõ¥Êñ∞ÊèêÁ§∫"
      width="400px"
      center
  >
    <p>ÂΩìÂâçÁâàÊú¨Ôºö<strong>{{ currentVersion }}</strong></p>
    <p>ÊúÄÊñ∞ÁâàÊú¨Ôºö<strong>{{ latestVersion }}</strong></p>
    <template #footer>
      <el-button @click="showUpdateDialog = false">ÂèñÊ∂à</el-button>
      <el-button type="primary" @click="confirmUpdate">Á´ãÂç≥Êõ¥Êñ∞</el-button>
    </template>
  </el-dialog>
</template>

<script>
import {Fold, Expand} from '@element-plus/icons-vue';
import {ElMessage} from "element-plus";
export default {
  components: {
    Fold,
    Expand,
  },
  mounted() {
    this.isDarkMode = localStorage.getItem('theme') === 'dark';
    this.currentVersion = localStorage.getItem('currentVersion');
    this.latestVersion = localStorage.getItem('latestVersion');
    this.needUpdate = localStorage.getItem('currentVersion') !== localStorage.getItem('latestVersion');
  },
  computed: {
    themeText() {
      return this.isDarkMode ? 'ÂºÄÁÅØüí°' : 'ÂÖ≥ÁÅØüåô';
    }
  },
  data() {
    return {
      needUpdate: false,
      showUpdateDialog: false,
      currentVersion: "",
      latestVersion: '',
      activeMenu: 'home',
      isCollapsed: false, // Controls sidebar state
      isDarkMode: localStorage.getItem('theme') === 'dark' // ËØªÂèñÊú¨Âú∞Â≠òÂÇ®ÁöÑ‰∏ªÈ¢òÁä∂ÊÄÅ
    };
  },
  methods: {
    confirmUpdate() {
      this.$axios.post('/sys/updateVersion', {})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 5000
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error);
          })
          .finally(() => {
            this.needUpdate = false
            this.showUpdateDialog = false
            this.logout();
          })
    },
    toggleTheme() {
      const isDark = document.documentElement.classList.toggle('dark')
      localStorage.setItem('theme', isDark ? 'dark' : 'light')
      this.isDarkMode = isDark; // Êõ¥Êñ∞Áä∂ÊÄÅ
    },
    handleSelect(index) {
      this.activeMenu = index;
      this.$router.push(`/dashboard/${index}`);
    },
    toggleSidebar() {
      this.isCollapsed = !this.isCollapsed;
    },
    showLogoutConfirm() {
      const confirmed = window.confirm('Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêó?');
      if (confirmed) {
        this.logout();
        sessionStorage.clear()
      } else {
        this.$message.info('ÂèñÊ∂àÈÄÄÂá∫');
      }
    },
    logout() {
      sessionStorage.clear()
      sessionStorage.removeItem('token');
      this.$router.push('/login');
    }
  }
};
</script>

<style scoped>
