}

.right-buttons {
  margin-left: auto; /* ËÆ©ÊåâÈíÆÁªÑÈù†Âè≥ */
  display: flex;
  gap: 10px; /* ÊåâÈíÆÈó¥Ë∑ù */
}
</style>
